@mixin font($size, $weight) {
  font-size: $size;
  font-weight: $weight;
}

@mixin absolute($top, $right, $bottom, $left, $width, $height) {
  position: absolute;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
  width: $width;
  height: $height;
}

@mixin flex($flex-direction, $justify-content, $align-items) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin icon($wh, $img-name) {
  content: "";
  display: block;
  background: url(../img/icon/#{$img-name}.png) center / #{$wh}px #{$wh}px no-repeat;
  width: $wh + px;
  height: $wh + px;
}

@mixin spriteBtn($img-name, $width, $height, $rows: 3, $columns: 1) {
  background: url(../img/bg/#{$img-name}.png) 0 0 / #{$width * $columns}px #{$height * $rows}px no-repeat;
  width: #{$width}px;
  height: #{$height}px;

  &:hover {
    background-position: 0 (-$height) + px;
  }
  &:active {
    background-position: 0 (-$height * 2) + px;
  }
}

@mixin borderGradient2P($start-color, $start-point, $end-color, $end-point, $px: 2px) {
  border-bottom: $px solid;
  border-image: linear-gradient(to bottom, $start-color $start-point, $end-color $end-point);
  border-image-slice: 1;
}
@mixin borderGradient3P($start-color, $start-point, $mid-color, $mid-point, $end-color, $end-point, $px: 2px) {
  border-bottom: $px solid;
  border-image: linear-gradient(to right, $start-color $start-point, $mid-color $mid-point, $end-color $end-point);
  border-image-slice: 1;
}

@mixin scrollBar() {
  &::-webkit-scrollbar {
    width: 20px;
    &-track {
      background-color: #fbfbf8;
    }
    &-thumb {
      border-radius: 4px;
      border-top: 2px solid #e0e7ef;
      border-bottom: 2px solid #e0e7ef;
      background-image: url(../img/bg/thumb.png);
      background-size: 20px 1px;
    }
    &-button {
      background: url(../img/splite.png) no-repeat 0 0 / 500%;
      width: 20px;
      height: 20px;

      &:start:decrement {
        display: block;
        height: 20px;
        background-position: -80px 0;
      }
      &:end:increment {
        display: block;
        height: 20px;
        background-position: -80px -20px;
      }
    }
  }
}

@mixin textEllipsis($line-clamp: 2) {
  overflow: hidden;
  text-overflow: ellipsis;
  @if ($line-clamp >= 2) {
    display: -webkit-box;
    -webkit-line-clamp: $line-clamp;
    -webkit-box-orient: vertical;
  } @else {
    display: block;
    white-space: nowrap;
  }
}
@mixin removeTextEllipsis {
  white-space: normal;
  text-overflow: initial;
  overflow: initial;
  display: block;
}
